.boxCard {
  background-color: #236ccd;
  margin: 5px;
  border: 3px solid;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  padding-top: 25px;
}

.boxCard:hover {
  -webkit-box-shadow: inset 0 0 0 25px #53a7ea;
          box-shadow: inset 0 0 0 25px #53a7ea;
}



